.slider{
  position: relative;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.image{
 height: 70vh;
  border-radius: 10px;
}

.right-arrow{
  position: absolute;
  top:50%;
  right : 25%;
  font-size: 3rem;
  color: #424242;
  z-index: 18;
  cursor: pointer;
  user-select: none;
}
.left-arrow{
  position: absolute;
  top:50%;
  left : 25%;
  font-size: 3rem;
  color: #424242;
  z-index: 18;
  cursor: pointer;
  user-select: none;
}
.slide{
  opacity: 0;
  transition-duration: 1s ease ;
}
.active-slide{
  opacity: 1;
  transition-duration:1s ;
  transform: scale(1.08);
}
@media only screen and (max-width: 450px) {

  .slider{
    width: 100%;
    text-align: center;
  
  }
  .image{
  width: 80%;
  height: 50vh;
  }
  .right-arrow{
    font-size: 2rem;
    right: 10px;
  }
  
  .left-arrow{
    left: 10px;
    font-size: 2rem;
  }
}